                    <el-input v-model="filtersAddAccident.name"></el-input>
                </el-form-item>
                <el-form-item label="事故地点">
                    <el-input v-model="filtersAddAccident.address"></el-input>
                </el-form-item>
                <el-form-item label="事故时间">
                    <el-date-picker
                        v-model="filtersAddAccident.create_time"
                        type="date"
                        value-format="yyyy-MM-dd"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="事故备注">
                    <el-input type="textarea" v-model="filtersAddAccident.remark"></el-input>
                </el-form-item>
            </el-form>
            
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogAddAccident = false">取 消</el-button>
                <el-button type="primary" @click="dialogSubmitAccident">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 退回 -->
        <el-dialog title="退回" :visible.sync="dialogGoBack" width="50%">
            <el-form :inline="true" :model="filtersGoBack">
                <el-tag type="danger">确认退回该工单吗？如确认退回请填写备注信息。</el-tag>
                <el-form-item label="事故备注">
                    <el-input type="textarea" v-model="filtersGoBack.msg"></el-input>
                </el-form-item>
            </el-form>
            
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogGoBack = false">取 消</el-button>
                <el-button type="primary" @click="">确 定</el-button>
            </span>
        </el-dialog>
        
    </div>
</template>

