                }
            },
            isShowVoucher(){
                return this.form.node_id >= 208
            },
            getFormData() {
                let _path = this.$route.path,
                    _query = this.$route.query;

                _path.indexOf('view') > -1 ? this.pageType='view' : 'edit'

                // 公司列表
                organizationListUl({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompany = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                this.loading=true
                API.apiAccidentClaimsView({id:_query.id}).then(res => {
                    this.loading=false
                    if(res.code == 0 && res.data){
                        Object.assign(this.form, res.data)
                        this.getExecUser()
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            getExecUser(){
                // 审核人
                let params = {
                    cp_id:this.form.cp_id,
                    get_type:'4',
                    node_id:this.form.node_id,
                }
                this.form.after_user_id = ''
                API.apiAccidentAuditUser(params).then(res => {
                    if(res.code == 0){
                        this.optionsExecUser = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSelectAuditor(val){
                this.getExecUser();
            },
            handelOutstanding(){
                let num = 0
                if(this.form.some_collect_cast.choose.length<=0){
                    this.form.some_collect_cast.together_cost = this.form.some_collect_cast.this_cost
                }else{
                    this.form.no_pay_money.forEach(item2=>{
                        this.form.some_collect_cast.choose.forEach(item=>{
                            if(item2.id==item){
                                num+=Number(item2.money)
                            }
                        })
                    })
                    this.form.some_collect_cast.together_cost = num+Number(this.form.some_collect_cast.this_cost)
                }
            },
            handleSubmit(submitType) {
                if(this.form.node_id==200 || this.form.node_id==0){
                    let params = {
                        id: this.form.id,
                        is_big_accident: this.form.is_big_accident,
                        respon_img: this.form.respon_img,
                        car_less_img: this.form.car_less_img,
                        three_sides_img: this.form.three_sides_img,
                        acc_list: this.form.acc_list,
                        submit_type: submitType,
                        exam_type: this.form.opinion,
                        after_user_id: this.form.after_user_id,
                        operation_uniqid: this.form.operation_uniqid,
                    }
                    API.apiAccidentClaimsAccInfoSave(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.node_id == 203){
                    let params = {
                        is_arr: this.form.is_arr,
                        arr_ids: this.form.arr_ids,
                        remark: this.form.remark,
                        other_arr_remark: this.form.other_arr_remark,
                        other_arr_money: this.form.other_arr_money,
                        all_arr_money: this.form.all_arr_money,
                        is_arr_bus_ids: this.form.is_arr_bus_ids,
                        submit_type: submitType,
                        exam_type: this.form.opinion,
                        after_user_id: this.form.after_user_id,
                        operation_uniqid: this.form.operation_uniqid,
                        id: this.form.id
                    }
                    API.apiAccidentClaimsAcInfoUpdate(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else if(this.form.node_id == 207){
                    let params = {
                        id: this.form.id,
                        payee: this.form.payee,
                        insur_money: this.form.insur_money,
                        arr_money: this.form.arr_money,
                        fact_pay_money: this.form.fact_pay_money,
                        pay_bank: this.form.pay_bank,
                        bank_account: this.form.bank_account,
                        pay_account: this.form.pay_account,
                        pay_remark: this.form.pay_remark,
                        entrust_img: this.form.entrust_img,
                        exam_type: this.form.opinion,
                        submit_type: submitType,
                        remark: this.form.remark,
                        after_user_id: this.form.after_user_id,
                        operation_uniqid: this.form.operation_uniqid,
                    }
                    API.apiAccidentClaimsAcInfoUpdate(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }else{
                    let payParams={
                        cashier_img: this.form.cashier_img,
                        cashier_remark: this.form.cashier_remark,
                    }
                    let params = {
                        id: this.form.id,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                        remark: this.form.remark,
                        reject_type: this.form.stage_id==7?'1':'2',
                        exam_type: this.form.opinion,
                        is_cashier: this.form.tran_type,
                        tran_name: this.form.tran_name,
                        tran_id: this.form.tran_id,
                        operation_uniqid: this.form.operation_uniqid,
                    }
                    if(this.form.node_id==208){
                        Object.assign(params, payParams)
                    }
                    API.apiAccidentClaimsAcInfoUpdate(params).then(res => {
                        if(res.code == 0){
                            this.$message.success('提交成功！')
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }
                
            },
            handleUploadSuccess_cashier_img(res, file, fileList){
                this.form.cashier_img = res.data.url
            },
            handleUploadSuccess_spot_money_img(res, file, fileList){
                this.form.spot_money_img = res.data.url
            },

            handleUploadSuccess_entrust_img(res, file, fileList){
                this.form.entrust_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess_respon_img(res, file, fileList){
                this.form.respon_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess_car_less_img(res, file, fileList){
                this.form.car_less_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess_three_sides_img(res, file, fileList){
                this.form.three_sides_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess_handle_img(res, file, fileList){
                this.form.handle_img.push({
                    uid: file.uid,
                    url: res.data.url
                })
            },
            handleUploadSuccess(res, file, fileList){
                this.drive_license_img = res.data.url
            },
            handleBeforeUpload(){

            },
            handleAddAccident() {
                this.dialogAddAccident = true;
            },
            dialogSubmitAccident(){
                this.dialogAddAccident = false
                this.form.acc_list.push(this.filtersAddAccident)
            },
            handleDelete(row, index){
                this.form.acc_list.splice(index, 1);
            }
        }
    }
</script>

